* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

/* 변수로 색상 지정 */
:root {
  --baseColor: white;
  --dominatColor: black;
  --accentColor: tomato;
}

section > h2 {
  color: tomato;
  text-align: center;
}

.none {
  list-style: none;
}

.table_sub_title1 {
  height: 50px;
  background-color: tomato;
  line-height: 50px;
  color: white;
}

.table_sub_title2 {
  background-color: tomato;
  color: white;
  width: 320px;
}

.table_sub_title3 {
  background-color: salmon;
  width: 50px;
  text-align: center;
  font-weight: bold;
}

.table_sub_title4 {
  background-color: salmon;
  height: 200px;
  font-weight: bold;
}

.table_sub_title5 {
  background-color: salmon;
  font-weight: bold;
}

.table_sub_title6 {
  background-color: salmon;
  font-weight: bold;
}

table {
  width: 50%;
  min-width: 30%;
  border-spacing: none;
  border-collapse: collapse;
}

caption {
  text-align: left;
  height: 2em;
}

th,
td {
  border: 0.1em solid var(--dominatColor);
  padding: 0.5em;
}

tbody th {
  text-align: left;
}

.del {
  color: #ddd;
}

.point {
  font-weight: bold;
  color: var(--accentColor);
}

/* ******************************************************* */

h1 {
  color: var(--accentColor);
  background-color: var(--dominatColor);
  line-height: 2em;
  text-align: center;
}

h2 {
  text-align: center;
  font-size: 1em;
  height: 3em;
  color: var(--accentColor);
  margin-top: 1em;
}

article {
  padding: 1em;
}

h3 {
  margin-bottom: 1em;
}

article ul,
article ol {
  padding-left: 2em;
}

section {
  margin-top: 2em;
}

.overview {
  padding: 0.5em;
}

h4 {
  height: 2em;
}

.tip {
  padding: 0.5em 1em;
}

h5 {
  font-size: 1em;
}
